import Vue from "vue";
import VueRouter from "vue-router";

function ifAuthenticated(to, from, next) {
  const currentUser = window.localStorage.getItem('token')
  if (currentUser) {
    next()
  } else {
    next({
      name: 'login',
    })
  }
}
